const IconLitRenderer = {};

const block0 = (context) => { return html`<span 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"		class="${lit.ifTruthy(context.classes.main)}"		style="${lit.ifTruthy(context.styles.main)}"		role="${lit.ifTruthy(context.role)}"		aria-label="${lit.ifTruthy(context.label)}"		tabindex="${lit.ifTruthy(context.tabIndex)}"		title="${lit.ifTruthy(context.tooltip)}"		data-sap-ui-icon-content="${lit.ifTruthy(context.iconContent)}"		aria-expanded="${lit.ifTruthy(context.ariaExpanded)}"		aria-labelledby="${lit.ifTruthy(context.ariaLabelledBy)}"></span>`; };
