
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const TextAreaLitRenderer = {};

const block0 = (context) => { return html`<div 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"	class="${lit.ifTruthy(context.classes.main)}"	style="${lit.ifTruthy(context.styles.main)}"	aria-invalid="${lit.ifTruthy(context.ariaInvalid)}"	aria-labelledBy="${lit.ifTruthy(context.ariaLabelledBy)}">	${ context.ctr.growing ? block1(context) : undefined }<textarea		id="${lit.ifTruthy(context.ctr._id)}-inner"		class="${lit.ifTruthy(context.classes.inner)}"		style="${lit.ifTruthy(context.styles.inner)}"		placeholder="${lit.ifTruthy(context.ctr.placeholder)}"		?disabled="${lit.ifTruthy(context.ctr.disabled)}"		rows="${lit.ifTruthy(context.ctr.rows)}"		cols="${lit.ifTruthy(context.cols)}"		wrap="${lit.ifTruthy(context.wrapping)}"		maxlength="${lit.ifTruthy(context.maxLength)}"		.value="${lit.ifTruthy(context.ctr.value)}"		data-sap-focus-ref></textarea>	${ context.ctr.showExceededText ? block4(context) : undefined }</div>`; };
const block1 = (context) => { return html`<div id="${lit.ifTruthy(context.ctr._id)}-mirror" style="${lit.ifTruthy(context.styles.mirror)}" class="${lit.ifTruthy(context.classes.mirror)}" aria-hidden="true">			${ repeat(context.textTokens, undefined, (item, index) => block2(item, index, context)) }</div>	`; };
const block2 = (item, index, context) => { return html`${lit.ifTruthy(item.text)}${ !item.last ? block3(item, index, context) : undefined }`; };
const block3 = (item, index, context) => { return html`<br/>				`; };
const block4 = (context) => { return html`<span class="${lit.ifTruthy(context.classes.exceededText)}">${lit.ifTruthy(context.exceededText)}</span>	`; };
const renderMe = block0;

TextAreaLitRenderer.render = renderMe;

export default TextAreaLitRenderer;