import KeyCodes from "@openui5/sap.ui.core/dist/sap/ui/events/KeyCodes";
import ListItemType from "./types/ListItemType";
import ListItemT from "./types/ListItem";
import ListMode from "./types/ListMode";
import ListItemBase from "./ListItemBase";
import "./RadioButton";
import "./CheckBox";
import "./Button";

// Styles
import "./themes/sap_belize/ListItem.less";
import "./themes/sap_fiori_3/ListItem.less";

/**
 * @public
 */
const metadata = {
	T: ListItemT,
	abstract: true,
	properties: /** @lends  sap.ui.webcomponents.main.ListItem.prototype */ {

		/**
		 * Defines the selected state of the <code>ListItem</code>.
		 * @type {boolean}
		 * @public
		 */
		selected: {
			type: Boolean
		},

		/**
		 * Defines the visual indication and behavior of the list items.
		 * Available options are <code>Active</code> and <code>Inactive</code>.
		 * @type {string}
		 * @public
		*/
		type: {
			type: ListItemType,
			defaultValue: ListItemType.Inactive
		},

		_active: {
			type: Boolean
		},

		_mode: {
			type: ListMode,
			defaultValue: ListMode.None
		},

		_selectionControl: {
			type: Object
		}
	},
	events: {
		_press: {},
		_detailPress: {},
		_focused: {},
		_focusForward: {}
	}
};

/**
 * @class
 * A class to serve as a base
 * for the <code>StandardListItem</code> and <code>CustomListItem</code> classes.
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.ListItem
 * @public
 */
class ListItem extends ListItemBase {

	static get metadata() {
		return metadata;
	}

	constructor(state) {
		super(state);

		// TODO: discuss how to pass methods to the template
		// as the template context does not contain web component reference
		this._state.fnOnDelete = this.onDelete.bind(this);
	}

	onBeforeRendering() {}

	onkeydown(event) {
		const spaceUsed = event.which === KeyCodes.SPACE;
		const enterUsed = event.which === KeyCodes.ENTER;
		const itemActive = this.type === ListItemType.Active;

		if (spaceUsed) {
			event.preventDefault();
		}

		if ((spaceUsed || enterUsed) && itemActive) {
			this.activate();
		}

		if (enterUsed) {
			this.fireEvent('_press', { item: this, selected: this.selected });
		}
	}

	onkeyup(event) {
		const spaceUsed = event.which === KeyCodes.SPACE;
		const enterUsed = event.which === KeyCodes.ENTER;

		if (spaceUsed || enterUsed) {
			this.deactivate();
		}

		if (spaceUsed) {
			this.fireEvent('_press', { item: this, selected: this.selected });
		}
	}

	ondown() {
		this.activate();
	}

	onup() {
		this.deactivate();
	}

	ontap() {
		this.fireEvent('_press', { item: this, selected: this.selected });
	}

	onfocusout() {
		this.deactivate();
	}

	onmouseout() {
		this.deactivate();
	}

	activate() {
		if (this.type === ListItemType.Active) {
			this._active = true;
		}
	}

	deactivate() {
		this._active = false;
	}

	onDelete(event) {
		this.fireEvent("_selectionRequested", { item: this, selected: event.selected });
	}
}

export default ListItem;