
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const ListLitRenderer = {};

const block0 = (context) => { return html`<div 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"	class="${lit.ifTruthy(context.classes.main)}"	style="${lit.ifTruthy(context.styles.main)}"><!-- header toolbar -->	${ context.ctr.headerToolbar ? block1(context) : undefined }${ context.shouldRenderH1 ? block2(context) : undefined }<div id="${lit.ifTruthy(context.ctr._id)}-before" tabindex="0" class="sapMListDummyArea"></div><ul id="${lit.ifTruthy(context.ctr._id)}-listUl" class="${lit.ifTruthy(context.classes.ul)}">		${ repeat(context.ctr.items, undefined, (item, index) => block3(item, index, context)) }${ context.showNoDataText ? block4(context) : undefined }</ul>	${ context.ctr.footerText ? block5(context) : undefined }<div id="${lit.ifTruthy(context.ctr._id)}-after" tabindex="0" class="sapMListDummyArea"></div></div>`; };
const block1 = (context) => { return html`<slot name="${lit.ifTruthy(context.ctr.headerToolbar._slot)}" />	`; };
const block2 = (context) => { return html`<header id="${lit.ifTruthy(context.ctr._id)}-header" class="sapMListHdr sapMListHdrText">			${lit.ifTruthy(context.ctr.headerText)}</header>	`; };
const block3 = (item, index, context) => { return html`<slot name="${lit.ifTruthy(item._slot)}" />		`; };
const block4 = (context) => { return html`<li id="${lit.ifTruthy(context.ctr._id)}-nodata" class="${lit.ifTruthy(context.classes.noData)}" tabindex="${lit.ifTruthy(context.noDataTabIndex)}"><div id="${lit.ifTruthy(context.ctr._id)}-nodata-text" class="sapMListNoDataText">					${lit.ifTruthy(context.ctr.noDataText)}</div></li>		`; };
const block5 = (context) => { return html`<footer id="${lit.ifTruthy(context.ctr._id)}-footer" class="sapMListFtr">			${lit.ifTruthy(context.ctr.footerText)}</footer>	`; };
const renderMe = block0;

ListLitRenderer.render = renderMe;

export default ListLitRenderer;