
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const PanelLitRenderer = {};

const block0 = (context) => { return html`<div 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"		data-sap-ui-fastnavgroup="true"		class="${lit.ifTruthy(context.classes.main)}"		style="${lit.ifTruthy(context.styles.main)}"		role="${lit.ifTruthy(context.accRole)}"><!-- header: either header toolbar, or h1 with header text -->	${ context.ctr.fixed ? block1(context) : block4(context) }<!-- info toolbar -->	${ context.ctr.infoToolbar ? block7(context) : undefined }<!-- content area --><div class="${lit.ifTruthy(context.classes.content)}" tabindex="-1" style="${lit.ifTruthy(context.styles.content)}">		${ repeat(context.ctr.content, undefined, (item, index) => block8(item, index, context)) }</div></div>`; };
const block1 = (context) => { return html`${ context.ctr.headerToolbar ? block2(context) : undefined }${ context.shouldRenderH1 ? block3(context) : undefined }`; };
const block2 = (context) => { return html`<div class="sapMPanelHdrToolbar"><slot name="${lit.ifTruthy(context.ctr.headerToolbar._slot)}"></slot></div>	`; };
const block3 = (context) => { return html`<h1 id="${lit.ifTruthy(context.ctr._id)}-header" class="sapMPanelHdr">			${lit.ifTruthy(context.ctr.headerText)}</h1>	`; };
const block4 = (context) => { return html`<header class="${lit.ifTruthy(context.classes.header)}"><ui5-icon					functional					class="${lit.ifTruthy(context.classes.icon)}"					src="${lit.ifTruthy(context.ctr._icon.src)}"					@press="${lit.ifTruthy(context.ctr._icon.onPress)}"			></ui5-icon>			${ context.ctr.headerToolbar ? block5(context) : undefined }${ context.shouldRenderH1 ? block6(context) : undefined }</header>	`; };
const block5 = (context) => { return html`<div class="sapMPanelHdrToolbar"><slot name="${lit.ifTruthy(context.ctr.headerToolbar._slot)}"></slot></div>	`; };
const block6 = (context) => { return html`<h1 id="${lit.ifTruthy(context.ctr._id)}-header" class="sapMPanelHdr">			${lit.ifTruthy(context.ctr.headerText)}</h1>	`; };
const block7 = (context) => { return html`<div class="${lit.ifTruthy(context.classes.info)}" style="${lit.ifTruthy(context.styles.info)}"><slot name="${lit.ifTruthy(context.ctr.infoToolbar._slot)}"></slot></div>	`; };
const block8 = (item, index, context) => { return html`<slot name="${lit.ifTruthy(item._slot)}"></slot>		`; };
const renderMe = block0;

PanelLitRenderer.render = renderMe;

export default PanelLitRenderer;