import WebComponent from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/WebComponent";
import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";

/**
 * @public
 */
const metadata = {
	tag: "ui5-toolbar-spacer",
	noShadowDOM: true,
	properties: /** @lends  sap.ui.webcomponents.main.ToolbarSpacer.prototype */ {

	}
};

/**
 * @class
 * Adds horizontal space between the items used within a {@link Toolbar}.
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.ToolbarSpacer
 * @public
 */
class ToolbarSpacer extends WebComponent {

	static get metadata() {
		return metadata;
	}
}

Core.boot().then(_ => {
	ToolbarSpacer.define();
});

export default ToolbarSpacer;
