import WebComponent from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/WebComponent";
import CardTemplateContext from "./CardTemplateContext";
import CardRenderer from "./build/compiled/CardRenderer.lit";
import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";

import "./themes/sap_belize/Card.less";
import "./themes/sap_fiori_3/Card.less";

/**
 * @public
 */
const metadata = {
	tag: "ui5-card",
	styleUrl: [
		"Card.css"
	],
	properties: /** @lends  sap.ui.webcomponents.main.Card.prototype */ {
		/**
		 * Defines the card manifest.
		 * @type {Object}
		 * @public
		 */
		manifest: {
			type: String
		}
	}
};

/**
 * @class
 * A control that represents header and content area as a card.
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.Card
 * @public
 */
class Card extends WebComponent {

	static get metadata() {
		return metadata;
	}

	static get renderer() {
		return CardRenderer;
	}

	constructor(state) {
		super(state);
	}

	static get calculateTemplateContext() {
		return CardTemplateContext.calculate;
	}
}

Core.boot().then(_ => {
	Card.define();
});

export default Card;
