
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const DialogLitRenderer = {};

const block0 = (context) => { return html`<span 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}" class="${lit.ifTruthy(context.classes.frame)}"><span id="${lit.ifTruthy(context.ctr._id)}-firstfe" tabindex="0"></span><div style="${lit.ifTruthy(context.styles.zindex)}" class="${lit.ifTruthy(context.classes.dialogParent)}"><div tabindex="-1" aria-labelledby="${lit.ifTruthy(context.headerId)}" role="dialog" class="${lit.ifTruthy(context.classes.main)}">			${ !context.ctr.hideHeader ? block1(context) : undefined }<section class="sapMDialogSection"><div style="${lit.ifTruthy(context.styles.content)}" class="sapMPopupContent"><div class="sapMPopupScroll">						${ repeat(context.ctr.content, undefined, (item, index) => block4(item, index, context)) }</div></div></section>			${ context.ctr.footer ? block5(context) : undefined }</div></div><span id="${lit.ifTruthy(context.ctr._id)}-lastfe" tabindex="0"></span><div tabindex="0" id="${lit.ifTruthy(context.ctr._id)}-blocklayer" style="${lit.ifTruthy(context.styles.blockLayer)}" class="${lit.ifTruthy(context.classes.blockLayer)}"></div></span>`; };
const block1 = (context) => { return html`<header>			${ context.ctr.header ? block2(context) : block3(context) }</header>	`; };
const block2 = (context) => { return html`<div role="heading" class="sapMPopupHeader"><slot name="${lit.ifTruthy(context.ctr.header._slot)}"></slot></div>			`; };
const block3 = (context) => { return html`<h2 role="heading" class="sapMPopupHeader sapMPopupHeaderText">${lit.ifTruthy(context.ctr.headerText)}</h2>			`; };
const block4 = (item, index, context) => { return html`<slot name="${lit.ifTruthy(item._slot)}"></slot>						`; };
const block5 = (context) => { return html`<footer><div class="sapMPopupFooter"><slot name="${lit.ifTruthy(context.ctr.footer._slot)}"></slot></div></footer>	`; };
const renderMe = block0;

DialogLitRenderer.render = renderMe;

export default DialogLitRenderer;