import DataType from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/types/DataType";

const InputTypes = {

	Text: "Text",

	Email: "Email",

	Number: "Number",

	Password: "Password",

	Tel: "Tel",

	URL: "URL"
};

class InputType extends DataType {

	static isValid(value) {
		return !!InputTypes[value];
	}
}

InputType.generataTypeAcessors(InputTypes);

export default InputType;
