import WebComponent from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/WebComponent";
import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";
import TableCell from "./TableCell";
import TableRowRenderer from "./build/compiled/TableRowRenderer.lit";

// Styles
import "./themes/sap_belize/TableRow.less";
import "./themes/sap_fiori_3/TableRow.less";

/**
 * @public
 */
const metadata = {
	tag: "ui5-table-row",
	styleUrl: [
		"TableRow.css"
	],
	slots: {
		/**
		 * Defines the cells of the <code>Row</code>
		 * @type {TableCell}
		 * @public
		 */
		cells: {
			type: TableCell,
			multiple: true
		}
	},
	properties: /** @lends sap.ui.webcomponents.main.TableRow.prototype */ {
		_columnsInfo: {
			type: Object,
			multiple: true
		},
		_tabIndex: {
			type: String,
			defaultValue: "-1"
		}
	},
	events: {
		_focused: {}
	}
};

/**
 * @class
 *
 * Constructor for a new Row.
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.TableRow
 * @public
 */
class TableRow extends WebComponent {

	static get metadata() {
		return metadata;
	}

	static get renderer() {
		return TableRowRenderer;
	}

	static get calculateTemplateContext() {
		return (state) => {
			let context = {
				ctr: state,
				visibleCells: [],
				popinCells: [],
				columnInfo: state._columnsInfo,
				classes: {
					main: {
						"sapWCTableRow": true,
						"sapWCTableRowWithBorder": true
					},
					popin: {
						"sapWCTablePopinRow": true
					},
					popinTitle: {
						"sapWCTablePopinTitle": true
					},
					cellWrapper: {
						"sapMWCTableRowCellContainer": true
					}
				},
				styles: {
					main: {
						"grid-template-columns": ""
					},
					popin: {
						"grid-column-end": 6
					}
				}
			};

			this.calculateCellsStyles(context);

			context.visibleColumnLength = context.visibleCells.length + 1;


			return context;
		};
	}

	static calculateCellsStyles(context) {
		context.columnInfo.forEach((info, index) => {
			if (info.visible) {
				// width of cells
				context.styles.main["grid-template-columns"] += `minmax(0, ${info.width || "1fr"}) `;

				context.visibleCells.push(context.ctr.cells[index]);

				// align of cells
				context.ctr.cells[index]._hAlign = info.hAlign;

				context.ctr.cells[index]._firstInRow = (index === 0);
			} else if (info.demandPopin) {
				context.popinCells.push({
					cell: context.ctr.cells[index],
					popinText: info.popinText
				});
			}
		}, this);

		const lastVisibleCell = context.visibleCells[context.visibleCells.length - 1];

		lastVisibleCell && (lastVisibleCell._lastInRow = true);
	}

	onfocusin(event) {
		this.fireEvent('_focused', event);
	}
}

Core.boot().then(_ => {
	TableRow.define();
});

export default TableRow;