import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";
import ListItem from "./ListItem";
import CustomListItemTemplateContext from "./CustomListItemTemplateContext";
import CustomListItemRenderer from "./build/compiled/CustomListItemRenderer.lit";
import CustomListItemT from "./types/CustomListItem";

/**
 * @public
 */
const metadata = {
	T: CustomListItemT,
	tag: "ui5-li-custom",
	styleUrl: [
		"ListItemBase.css",
		"ListItem.css"
	],
	defaultSlot: "content",
	slots: {

		/**
		 * Defines the content of the <code>CustomListItem</code>.
		 * @type {HTMLElement}
		 * @public
		 */
		content: {
			type: HTMLElement,
			multiple: true
		}
	},
	properties: /** @lends sap.ui.webcomponents.main.CustomListItem.prototype */ {
	}
};

/**
 * @class
 *
 * A class for a new <code>CustomListItem</code>.
 *
 * <code>CustomListItem</code> has content and it can be used to customize the <code>StandardListItem</code>.
 * <code>List</code> mode and <code>ListItem</code> type are applied to <code>CustomListItem</code> as well.
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.CustomListItem
 * @public
 */
class CustomListItem extends ListItem {

	static get metadata() {
		return metadata;
	}

	static get renderer() {
		return CustomListItemRenderer;
	}

	constructor(state) {
		super(state);
	}

	static get calculateTemplateContext() {
		return CustomListItemTemplateContext.calculate;
	}
}

Core.boot().then(_ => {
	CustomListItem.define();
});

export default CustomListItem;
