
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const TableLitRenderer = {};

const block0 = (context) => { return html`<div 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"	style="display: grid; place-items: center;"><!-- columns --><div class="${lit.ifTruthy(context.classes.main)}" style="${lit.ifTruthy(context.styles.main)}">		${ repeat(context.visibleColumns, undefined, (item, index) => block1(item, index, context)) }</div><!-- rows -->	${ repeat(context.ctr.rows, undefined, (item, index) => block2(item, index, context)) }</div>`; };
const block1 = (item, index, context) => { return html`<div class="sapWCTableColumnWrapper"><slot name="${lit.ifTruthy(item._slot)}"></slot></div>		`; };
const block2 = (item, index, context) => { return html`<div style="width: 100%"		><slot name="${lit.ifTruthy(item._slot)}"></slot></div>	`; };
const renderMe = block0;

TableLitRenderer.render = renderMe;

export default TableLitRenderer;