import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";
import ListItemBase from "./ListItemBase";
import ListItemBaseT from "./types/ListItemBase";

// Template
import GroupHeaderListItemRenderer from "./build/compiled/GroupHeaderListItemRenderer.lit";
import GroupHeaderListItemTemplateContext from "./GroupHeaderListItemTemplateContext";

// Styles
import "./themes/sap_belize/GroupHeaderListItem.less";
import "./themes/sap_fiori_3/GroupHeaderListItem.less";

/**
 * @public
 */
const metadata = {
	T: ListItemBaseT,
	tag: "ui5-li-group-header",
	styleUrl: [
		"ListItemBase.css",
		"GroupHeaderListItem.css"
	],
	usesNodeText: true,
	properties: /** @lends  sap.ui.webcomponents.main.GroupHeaderListItem.prototype */ {
	},
	events: {
	}
};

/**
 * @class
 *  A class to represent a  <code>GroupHeaderListItem</code>.
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.GroupHeaderListItem
 * @public
 */
class GroupHeaderListItem extends ListItemBase {

	constructor(state) {
		super(state);
	}

	static get renderer() {
		return GroupHeaderListItemRenderer;
	}

	static get metadata() {
		return metadata;
	}

	static get calculateTemplateContext() {
		return GroupHeaderListItemTemplateContext.calculate;
	}
}

Core.boot().then(_ => {
	GroupHeaderListItem.define();
});

export default GroupHeaderListItem;