import WebComponent from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/WebComponent";
import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";
import CalendarHeaderTemplateContext from "./CalendarHeaderTemplateContext";
import Button from "./Button";
import ButtonType from "./types/ButtonType";
import CalendarHeaderRenderer from "./build/compiled/CalendarHeaderRenderer.lit";
import ShadowDOM from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/compatibility/ShadowDOM";

// Styles
import calendarHeaderBelize from "./themes/sap_belize/CalendarHeader.less";
import "./themes/sap_fiori_3/CalendarHeader.less";

ShadowDOM.registerStyle("sap_belize", "CalendarHeader.css", calendarHeaderBelize);

const metadata = {
	tag: "ui5-calendar-header",
	styleUrl: [
		"CalendarHeader.css",
		"Button.css",
		"Icon.css"
	],
	properties: {
		monthText: {
			type: String
		},
		yearText: {
			type: String
		},
		_btnPrev: {
			type: Object
		},
		_btnNext: {
			type: Object
		},
		_btn1: {
			type: Object
		},
		_btn2: {
			type: Object
		}
	},
	events : {
		pressPrevious : {},
		pressNext : {},
		btn1Press : {},
		btn2Press : {}
	}
};

class CalendarHeader extends WebComponent {

	static get metadata() {
		return metadata;
	}

	static get renderer() {
		return CalendarHeaderRenderer;
	}

	constructor(state) {
		super(state);

		this._btnPrev = {};
		this._btnPrev.icon = "sap-icon://slim-arrow-left";
		this._btnPrev.onPress = this._handlePrevPress.bind(this);

		this._btnNext = {};
		this._btnNext.icon = "sap-icon://slim-arrow-right";
		this._btnNext.onPress = this._handleNextPress.bind(this);

		this._btn1 = {};
		this._btn1.type = ButtonType.Transparent;
		this._btn1.onPress = this._handleBtn1Press.bind(this);

		this._btn2 = {};
		this._btn2.type = ButtonType.Transparent;
		this._btn2.onPress = this._handleBtn2Press.bind(this);
	}

	onBeforeRendering() {
		this._btnPrev._customAttributes = {
			"tabindex": '-1'
		};
		this._btnNext._customAttributes = {
			"tabindex": '-1'
		};
		this._btnPrev._customClasses = ['sapUiCalHeadPrev'];
		this._btnNext._customClasses = ['sapUiCalHeadNext'];
		this._btn1.text = this.monthText;
		this._btn2.text = this.yearText;
	}

	_handlePrevPress(event) {
		this.fireEvent("pressPrevious", event);
	}

	_handleNextPress(event) {
		this.fireEvent("pressNext", event);
	}

	_handleBtn1Press(event) {
		this.fireEvent("btn1Press", event);
	}

	_handleBtn2Press(event) {
		this.fireEvent("btn2Press", event);
	}

	static get calculateTemplateContext() {
		return CalendarHeaderTemplateContext.calculate;
	}
}

Core.boot().then(async _ => {
	await Button.define();
	CalendarHeader.define();
});

export default CalendarHeader;