import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";

const dir = Core.getConfiguration().getRTL() ? "rtl" : "ltr";

	class DatePickerTemplateContext {

		static calculate(state) {

			let context = {
				ctr: state,
				classes: {
					main: {
						"sapMDP": true,
						"sapMInputBase": true,
						"sapMInputBaseWidthPadding": true,
						"sapMInputVH": true
					},
					wrapper: {
						"sapMInputDivWrapper": true
					},
					inner: {
						"sapMInputBaseInner": true
					}
				},
				styles: {
					main: {
						width: "100%"
					}
				},
				dir
			};

			return context;
		}
	}

export default DatePickerTemplateContext;