import WebComponent from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/WebComponent";
import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";
import YearPickerTemplateContext from "./YearPickerTemplateContext";
import ItemNavigation from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/delegate/ItemNavigation";
import Integer from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/types/Integer";
import DateFormat from "@openui5/sap.ui.core/dist/sap/ui/core/format/DateFormat";
import CalendarType from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/dates/CalendarType";
import CalendarDate from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/dates/CalendarDate";
import YearPickerRenderer from "./build/compiled/YearPickerRenderer.lit";
import ShadowDOM from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/compatibility/ShadowDOM";

import calendarBelize from "./themes/sap_belize/Calendar.less";
import "./themes/sap_fiori_3/Calendar.less";

ShadowDOM.registerStyle("sap_belize", "Calendar.css", calendarBelize);

/**
 * @public
 */
const metadata = {
	tag: "ui5-year-picker",
	styleUrl: [
		"Calendar.css"
	],
	properties: /** @lends  sap.ui.webcomponents.main.YearPicker.prototype */ {
		/**
		 * A UNIX timestamp - seconds since 00:00:00 UTC on Jan 1, 1970.
		 * @type {Integer}
		 * @public
		 */
		timestamp: {
			type: Integer
		},
		/**
		 * Sets a calendar type used for display.
		 * If not set, the calendar type of the global configuration is used.
		 * @type {String}
		 * @public
		 */
		primaryCalendarType: {
			type: CalendarType
		},
		_selectedYear: {
			type: Integer
		},
		_years: {
			type: Object,
			multiple: true
		},
		_hidden: {
			type: Boolean
		}
	},
	events: /** @lends  sap.ui.webcomponents.main.YearPicker.prototype */ {
		/**
		 * Fired when the user selects a new Date on the control.
		 * @public
		 * @event
		 */
		selectedYearChange: {}
	}
};

const ITEMS_MAX_OFFSET = 12;
const ITEMS_COUNT = 2 * ITEMS_MAX_OFFSET + 1;

/**
 * @class
 *
 * Displays years which can be selected.
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.YearPicker
 * @public
 */
class YearPicker extends WebComponent {

	static get metadata() {
		return metadata;
	}

	static get renderer() {
		return YearPickerRenderer;
	}

	constructor(state) {
		super(state);

		this._oLocale = Core.getConfiguration().getFormatSettings().getFormatLocale();

		this._itemNav = new ItemNavigation(this, { rowSize: 5 });
		this._itemNav.getItemsCallback = function() {
			return this._years;
		}.bind(this);
		this._itemNav.setItemsCallback = function(items) {
			this._years = items;
		}.bind(this);

		this._itemNav.attachEvent(ItemNavigation.BORDER_REACH, this._handleItemNavigationBorderReach.bind(this));

		this._delegates.push(this._itemNav);
	}

	onBeforeRendering() {
		const oYearFormat = DateFormat.getDateInstance({ format: "y", calendarType: this._primaryCalendarType }, this._oLocale);
		const oCalDate = this._calendarDate;
		oCalDate.setMonth(0);
		oCalDate.setDate(1);
		oCalDate.setYear(oCalDate.getYear() - ITEMS_MAX_OFFSET - 1);
		const years = [];
		let timestamp;

		if (this._selectedYear === undefined) {
			this._selectedYear = this._year;
		}

		for (let i = 0; i <= 2 * ITEMS_MAX_OFFSET; i++) {
			oCalDate.setYear(oCalDate.getYear() + 1);
			timestamp = oCalDate.valueOf() / 1000;

			let year = {
				timestamp: timestamp.toString(),
				id: this._state._id + "-y" + (timestamp),
				year: oYearFormat.format(oCalDate.toLocalJSDate()),
				classes: "sapUiCalItem"
			};

			if (oCalDate.getYear() === this._selectedYear) {
				year.classes += " sapUiCalItemSel";
			}
			years.push(year);
		}

		this._years = years;

		this._itemNav.init();
	}

	onAfterRendering() {
		this._itemNav.focusCurrent();
	}

	get _timestamp() {
		return this.timestamp !== undefined ? this.timestamp : Math.floor(new Date().getTime() / 1000);
	}

	get _localDate() {
		return new Date(this._timestamp * 1000);
	}

	get _calendarDate() {
		return CalendarDate.fromLocalJSDate(this._localDate, this._primaryCalendarType);
	}

	get _year() {
		return this._calendarDate.getYear();
	}

	get _primaryCalendarType() {
		return this.primaryCalendarType || Core.getConfiguration().getCalendarType();
	}

	onclick(event) {
		if (event.ui5target.className.indexOf('sapUiCalItem') > -1) {
			const timestamp = this.getTimestampFromDom(event.ui5target);
			this.timestamp = timestamp;
			this._selectedYear = this._year;
			this._itemNav.current = ITEMS_MAX_OFFSET;
			this.fireEvent('selectedYearChange', { timestamp });
		}
	}

	getTimestampFromDom(domNode) {
		const sTimestamp = domNode.getAttribute("data-sap-timestamp");
		return parseInt(sTimestamp, 10);
	}

	static get calculateTemplateContext() {
		return YearPickerTemplateContext.calculate;
	}

	onsapenter(event) {
		event.preventDefault();
		if (event.ui5target.className.indexOf('sapUiCalItem') > -1) {
			const timestamp = this.getTimestampFromDom(event.ui5target);
			this.timestamp = timestamp;
			this._selectedYear = this._year;
			this._itemNav.current = ITEMS_MAX_OFFSET;
			this.fireEvent('selectedYearChange', { timestamp });
		}
	}

	onsapspace(event) {
		event.preventDefault();
		if (event.ui5target.className.indexOf('sapUiCalItem') > -1) {
			const timestamp = this.getTimestampFromDom(event.ui5target);
			this._selectedYear = CalendarDate.fromLocalJSDate(new Date(timestamp * 1000), this._primaryCalendarType).getYear();
		}
	}

	_handleItemNavigationBorderReach(event) {
		const oCalDate = this._calendarDate;
		oCalDate.setMonth(0);
		oCalDate.setDate(1);

		if (event.end) {
			oCalDate.setYear(oCalDate.getYear() + ITEMS_COUNT);
		} else if (event.start) {
			oCalDate.setYear(oCalDate.getYear() - ITEMS_COUNT);
		}

		this.timestamp = oCalDate.valueOf() / 1000;
	}
}

Core.boot().then(_ => {
	YearPicker.define();
});

export default YearPicker;