
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const ToolbarLitRenderer = {};

const block0 = (context) => { return html`<div 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"	class="${lit.ifTruthy(context.classes.main)}"	tabindex="${lit.ifTruthy(context._tabIndex)}">	${ repeat(context.content, undefined, (item, index) => block1(item, index, context)) }</div>`; };
const block1 = (item, index, context) => { return html`<span class="${lit.ifTruthy(item.classes)}"><slot name="${lit.ifTruthy(item.child._slot)}"></slot></span>	`; };
const renderMe = block0;

ToolbarLitRenderer.render = renderMe;

export default ToolbarLitRenderer;