
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const TableRowLitRenderer = {};

const block0 = (context) => { return html`<div 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"	class="${lit.ifTruthy(context.classes.main)}"	style="${lit.ifTruthy(context.styles.main)}"	tabindex="${lit.ifTruthy(context.ctr._tabIndex)}">	${ repeat(context.visibleCells, undefined, (item, index) => block1(item, index, context)) }${ repeat(context.popinCells, undefined, (item, index) => block2(item, index, context)) }</div>`; };
const block1 = (item, index, context) => { return html`<div class="${lit.ifTruthy(context.classes.cellWrapper)}"><slot name="${lit.ifTruthy(item._slot)}"></slot></div>	`; };
const block2 = (item, index, context) => { return html`<div class="${lit.ifTruthy(context.classes.popin)}"			style="grid-column-end: ${lit.ifTruthy(context.visibleColumnLength)}" ><span class="${lit.ifTruthy(context.classes.popinTitle)}">${lit.ifTruthy(item.popinText)}</span><div class="wtf"><slot name="${lit.ifTruthy(item.cell._slot)}"></slot></div></div>	`; };
const renderMe = block0;

TableRowLitRenderer.render = renderMe;

export default TableRowLitRenderer;