import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";
import URI from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/types/URI";
import ListItem from "./ListItem";
import StandardListItemT from "./types/StandardListItem";
import Icon from "./Icon";

// Template
import StandardListItemRenderer from "./build/compiled/StandardListItemRenderer.lit";
import StandardListItemTemplateContext from "./StandardListItemTemplateContext";

/**
 * @public
 */
const metadata = {
	T: StandardListItemT,
	tag: "ui5-li",
	styleUrl: [
		"ListItemBase.css",
		"ListItem.css"
	],
	usesNodeText: true,
	properties: /** @lends  sap.ui.webcomponents.main.StandardListItem.prototype */  {

		/**
		 * Defines the icon of the <code>StandardListItem</code>.
		 * @type {string}
		 * @public
		 */
		iconSrc: {
			type: URI,
			defaultValue: null
		},

		/**
		 * Defines the text of the <code>StandardListItem</code>.
		 * @type {string}
		 * @public
		 */
		imageSrc: {
			type: URI,
			defaultValue: null
		},

		_iconControl: {
			type: Object
		}
	}
};

/**
 * @class
 * A class for a new <code>StandardListItem</code>.
 *
 * <code>StandardListItem</code> is a list item,
 * providing the most common use cases such as <code>text</code>, <code>image</code> and <code>icon</code>.
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.StandardListItem
 * @public
 */
class StandardListItem extends ListItem {

	constructor(state) {
		super(state);
	}

	static get renderer() {
		return StandardListItemRenderer;
	}

	static get metadata() {
		return metadata;
	}

	static get calculateTemplateContext() {
		return StandardListItemTemplateContext.calculate;
	}

	onBeforeRendering() {
		ListItem.prototype.onBeforeRendering.call(this, arguments);
		this._updateIcon();
	}

	_updateIcon() {
		if (this.iconSrc) {
			this._iconControl = {
				_customClasses: ["sapMLIBImgNav"]
			};
			this._iconControl.src = this.iconSrc;
		}
	}
}

Core.boot().then(async _ => {
	await Icon.define();
	StandardListItem.define();
});

export default StandardListItem;