import WebComponent from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/WebComponent";
import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";

import KeyCodes from "@openui5/sap.ui.core/dist/sap/ui/events/KeyCodes";
import ValueState from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/types/ValueState";
import CSSSize from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/types/CSSSize";
import RadioButtonGroup from "./RadioButtonGroup";

// Template
import RadioButtonRenderer from "./build/compiled/RadioButtonRenderer.lit";
import RadioButtonTemplateContext from "./RadioButtonTemplateContext";

// Styles
import "./themes/sap_belize/RadioButton.less";
import "./themes/sap_fiori_3/RadioButton.less";

/**
 * @public
 */
const metadata = {
	tag: "ui5-radiobutton",
	styleUrl: [
		"RadioButton.css"
	],
	properties: /** @lends  sap.ui.webcomponents.main.RadioButton.prototype */  {

		/**
		 * Defines the <code>displayOnly</code> state of the <code>RadioButton</code>.
		 * Takes effect when the <code>RadioButton</code> is not disabled.
		 * @type {boolean}
		 * @public
		 */
		disabled: {
			"type": Boolean
		},

		/**
		 * Defines if the <code>RadioButton</code> is read only, e.g. editable or not
		 * @type {boolean}
		 * @public
		 */
		readOnly: {
			"type": Boolean
		},

		/**
		 * Defines if the <code>RadioButton</code> is selected or not.
		 * @type {boolean}
		 * @public
		 */
		selected: {
			"type": Boolean
		},

		/**
		 * Defines the text of the <code>RadioButton</code>.
		 * @type  {string}
		 * @public
		 */
		text : {
			"defaultValue": null,
			"type": String
		},

		/**
		 * Defines the tooltip of the <code>RadioButton</code>.
		 * @type  {string}
		 * @public
		 */
		tooltip : {
			"defaultValue": "",
			"type": String
		},

		/**
		 * Defines the value state of the <code>RadioButton</code>.
		 * Available options are: 'Warning', 'Error' and 'None' (by default).
		 * @type {string}
		 * @public
		 */
		valueState : {
			"defaultValue": ValueState.None,
			"type": ValueState
		},

		/**
		 * Defines the width of the CheckBox text.
		 * @type {string}
		 * @public
		 */
		width : {
			"defaultValue": "",
			"type": CSSSize
		},

		/**
		 * Defines which group the <code>RadioButton</code> belongs.
		 * @type {string}
		 * @public
		 */
		group : {
			"defaultValue": "",
			"type": String
		},

		_label: {
			"type": Object
		}
	},
	events: /** @lends sap.ui.webcomponents.main.RadioButton.prototype */ {
		/**
		 * Fired, whenever the <code>RadioButton</code> selected state changes.
		 * @event
		 * @param {boolean} selected Whether the <code>RadioButton</code> is  or not.
		 * @public
		 */
		select: {
			detail : {
				selected : {type : "boolean"}
			}
		}
	}
};

/**
 * @class
 * A class to represent a <code>RadioButton</code>.
 *
 * <b> Overview </b>
 * Enables users to select a single option from a set of options.
 * <h3>Use the <code>RadioButton</code> when you quickly need to choose between at least two alternatives.</li>
 *
 * <h3>When not to use:</h4>
 * <ul>
 * <li>You want to select multiple values for the same option - use CheckBox instead.</li>
 * <li>When the options are mutually exclusive</li>
 * </ul>
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.RadioButton
 * @public
 */
class RadioButton extends WebComponent {

	static get metadata() {
		return metadata;
	}

	static get renderer() {
		return RadioButtonRenderer;
	}

	constructor(state) {
		super(state);
		this._label = {};
	}

	onBeforeRendering() {
		this.syncLabel();
		this.syncGroup();
	}

	syncLabel() {
		this._label = Object.assign({}, this._label);
		this._label.text = this.text;
	}

	syncGroup() {
		const oldGroup = this._group;
		const currentGroup = this.group;

		if (currentGroup === oldGroup) {
			return;
		}

		if (oldGroup) {
			// remove the control from the previous group
			RadioButtonGroup.removeFromGroup(this, oldGroup);
		}

		if (currentGroup) {
			// add the control to the existing group
			RadioButtonGroup.addToGroup(this, currentGroup);
		}

		this._group = this.group;
	}

	ontap() {
		return this.toggle();
	}

	onsapdown(event){
		const currentGroup = this.group;

		if (!currentGroup) {
			return;
		}

		event.preventDefault();
		RadioButtonGroup.selectNextItem(this, currentGroup);
	}

	onsapup(event){
		const currentGroup = this.group;

		if (!currentGroup) {
			return;
		}

		event.preventDefault();
		RadioButtonGroup.selectPreviousItem(this, currentGroup);
	}

	onsapleft(event){
		this.onsapup(event);
	}

	onsapright(event){
		this.onsapdown(event);
	}

	onkeydown(event) {
		if (event.keyCode === KeyCodes.SPACE ) {
			event.preventDefault();
		}

		if (event.keyCode === KeyCodes.ENTER) {
			this.toggle();
		}
	}

	onkeyup(event) {
		if (event.keyCode === KeyCodes.SPACE) {
			this.toggle();
		}
	}

	toggle() {
		if (!this.canToggle()) {
			return this;
		}

		if (!this.group) {
			this.selected = !this.selected;
			this.fireEvent("select", {selected: this.selected});
			return this;
		}

		RadioButtonGroup.selectItem(this, this.group);
		return this;
	}

	canToggle() {
		return !(this.disabled || this.readOnly || this.selected);
	}

	static get calculateTemplateContext() {
		return RadioButtonTemplateContext.calculate;
	}
}

Core.boot().then(_ => {
	RadioButton.define();
});

export default RadioButton;