import WebComponent from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/WebComponent";
import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";
import MonthPickerTemplateContext from "./MonthPickerTemplateContext";
import ItemNavigation from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/delegate/ItemNavigation";
import Integer from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/types/Integer";
import LocaleData from "@openui5/sap.ui.core/dist/sap/ui/core/LocaleData";
import CalendarType from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/dates/CalendarType";
import CalendarDate from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/dates/CalendarDate";
import MonthPickerRenderer from "./build/compiled/MonthPickerRenderer.lit";

//Styles
import "./themes/sap_belize/Calendar.less";
import "./themes/sap_fiori_3/Calendar.less";

/**
 * @public
 */
const metadata = {
	tag: "ui5-month-picker",
	styleUrl: [
		"Calendar.css"
	],
	properties: /** @lends  sap.ui.webcomponents.main.MonthPicker.prototype */ {
		/**
		 * A UNIX timestamp - seconds since 00:00:00 UTC on Jan 1, 1970.
		 * @type {Integer}
		 * @public
		 */
		timestamp: {
			type: Integer
		},
		/**
		 * Sets a calendar type used for display.
		 * If not set, the calendar type of the global configuration is used.
		 * @type {String}
		 * @public
		 */
		primaryCalendarType: {
			type: CalendarType
		},
		_months: {
			type: Object,
			multiple: true
		},
		_hidden: {
			type: Boolean
		}
	},
	events: /** @lends  sap.ui.webcomponents.main.MonthPicker.prototype */ {
		/**
		 * Fired when the user selects a new Date on the control.
		 * @public
		 * @event
		 */
		selectedMonthChange: {}
	}
};

/**
 * Month picker component.
 *
 * @class
 *
 * Displays months which can be selected.
 *
 * @extends sap.ui.webcomponents.base.Control
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.MonthPicker
 * @public
 */
class MonthPicker extends WebComponent {

	static get metadata() {
		return metadata;
	}

	static get renderer() {
		return MonthPickerRenderer;
	}

	constructor(state) {
		super(state);
		this._oLocale = Core.getConfiguration().getFormatSettings().getFormatLocale();

		this._oLocaleData = new LocaleData(this._oLocale);

		this._itemNav = new ItemNavigation(this, { rowSize: 3, cyclic: true});
		this._itemNav.getItemsCallback = function() {
			return this._months;
		}.bind(this);
		this._itemNav.setItemsCallback = function(items) {
			this._months = items;
		}.bind(this);

		this._delegates.push(this._itemNav);
	}

	onBeforeRendering() {
		const months = [];
		const oCalDate = CalendarDate.fromLocalJSDate(new Date(), this._primaryCalendarType);
		let timestamp;

		for (let i = 0; i < 12; i++) {
			oCalDate.setMonth(i);
			timestamp = oCalDate.valueOf() / 1000;

			let month = {
				timestamp: timestamp.toString(),
				id: this._state._id + "-m" + months.length,
				name: this._oLocaleData.getMonths("wide", this._primaryCalendarType)[i],
				classes: "sapUiCalItem"
			};

			if (this._month === i) {
				month.classes += " sapUiCalItemSel";
			}
			months.push(month);
		}

		this._months = months;

		this._itemNav.init();
	}

	onAfterRendering() {
		this._itemNav.focusCurrent();
	}

	get _timestamp() {
		return this.timestamp !== undefined ? this.timestamp : Math.floor(new Date().getTime() / 1000);
	}

	get _localDate() {
		return new Date(this._timestamp * 1000);
	}

	get _calendarDate() {
		return CalendarDate.fromLocalJSDate(this._localDate, this._primaryCalendarType);
	}

	get _month() {
		return this._calendarDate.getMonth();
	}

	get _primaryCalendarType() {
		return this.primaryCalendarType || Core.getConfiguration().getCalendarType();
	}

	onclick(event) {
		if (event.ui5target.className.indexOf('sapUiCalItem') > -1) {
			const timestamp = this.getTimestampFromDOM(event.ui5target);
			this.timestamp = timestamp;
			this._itemNav.current = this._month;
			this.fireEvent('selectedMonthChange', { timestamp });
		}
	}

	onsapenter(event) {
		event.preventDefault();
		if (event.ui5target.className.indexOf('sapUiCalItem') > -1) {
			const timestamp = this.getTimestampFromDOM(event.ui5target);
			this.timestamp = timestamp;
			this.fireEvent('selectedMonthChange', { timestamp });
		}
	}

	onsapspace(event) {
		event.preventDefault();
		if (event.ui5target.className.indexOf('sapUiCalItem') > -1) {
			this.timestamp = this.getTimestampFromDOM(event.ui5target);
		}
	}

	getTimestampFromDOM(domNode) {
		const oMonthDomRef = domNode.getAttribute("data-sap-timestamp");
		return parseInt(oMonthDomRef, 10);
	}

	static get calculateTemplateContext() {
		return MonthPickerTemplateContext.calculate;
	}
}

Core.boot().then(_ => {
	MonthPicker.define();
});

export default MonthPicker;