
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const CardLitRenderer = {};

const block0 = (context) => { return html`<div 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"		class="${lit.ifTruthy(context.classes.main)}"><header class="sapFCardHeader"><span role="img" aria-label="Avatar" class="sapFCardAvatar"><ui5-icon class="sapFCardHeaderIcon" src="${lit.ifTruthy(context.icon.src)}"></ui5-icon></span><div class="sapFCardHeaderText"><div class="sapFCardTitle">${lit.ifTruthy(context.title)}</div><div class="sapFCardSubtitle ">${lit.ifTruthy(context.subTitle)}</div></div><span class="sapFCardStatus">${lit.ifTruthy(context.status)}</span></header><section class="sapFCardContent"><ui5-list>			${ repeat(context.items, undefined, (item, index) => block1(item, index, context)) }</ui5-list></section></div>`; };
const block1 = (item, index, context) => { return html`<ui5-li-custom hide-border><div class="sapFCardListItem"><div class="sapFCardListItemTitle">${lit.ifTruthy(item.title)}</div><div class="sapFCardListItemDescription">${lit.ifTruthy(item.description)}</div></div></ui5-li-custom>			`; };
const renderMe = block0;

CardLitRenderer.render = renderMe;

export default CardLitRenderer;