import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";
import KeyCodes from "@openui5/sap.ui.core/dist/sap/ui/events/KeyCodes";

import Button from "./Button";
import ToggleButtonTemplateContext from "./ToggleButtonTemplateContext";
import ToggleButtonRenderer from "./build/compiled/ToggleButtonRenderer.lit";

import "./themes/sap_belize/ToggleButton.less";
import "./themes/sap_fiori_3/ToggleButton.less";

/**
 * @public
 */
const metadata = {
	tag: "ui5-toggle-button",
	styleUrl: [
		"Button.css",
		"ToggleButton.css"
	],
	usesNodeText: true,
	properties: /** @lends  sap.ui.webcomponents.main.ToggleButton.prototype */ {
		/**
		 * Specifies if the button is currently pressed.
		 * @type {boolean}
		 * @public
		 */
		pressed: {
			"type": Boolean
		}
	}
};

/**
 *
 * @class
 * An enhanced {@link Button} that can be toggled between pressed and normal state.
 *
 * Clicking or tapping a <code>ToggleButton</code> changes its state to <code>pressed</code>. The button returns to
 * its initial state when the user clicks or taps it again.
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.ToggleButton
 * @public
 */
class ToggleButton extends Button {

	static get metadata() {
		return metadata;
	}

	static get renderer() {
		return ToggleButtonRenderer;
	}

	constructor(state) {
		super(state);
	}

	ontap() {
		if (!this.disabled) {
			this.pressed = !this.pressed;
			this.fireEvent("press", {pressed: this.pressed});
		}
	}

	onkeyup(event) {

		if (event.which === KeyCodes.SPACE || event.which === KeyCodes.ENTER && !this.disabled) {
		this.pressed = !this.pressed;
		}
	}

	static get calculateTemplateContext() {
		return ToggleButtonTemplateContext.calculate;
	}
}

Core.boot().then(_ => {
	ToggleButton.define();
});

export default ToggleButton;
