
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const YearPickerLitRenderer = {};

const block0 = (context) => { return html`<div 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"	class="sapUiCalYearPicker"	role="grid"	aria-readonly="false"	aria-multiselectable="false"	style="${lit.ifTruthy(context.styles.main)}">    ${ repeat(context.ctr._years, undefined, (item, index) => block1(item, index, context)) }</div>`; };
const block1 = (item, index, context) => { return html`<div id="${lit.ifTruthy(item.id)}"			tabindex="${lit.ifTruthy(item._tabIndex)}"			data-sap-timestamp="${lit.ifTruthy(item.timestamp)}"			class="${lit.ifTruthy(item.classes)}"			style="width:20%"			role="gridcell"			aria-selected="false">				${lit.ifTruthy(item.year)}</div>    `; };
const renderMe = block0;

YearPickerLitRenderer.render = renderMe;

export default YearPickerLitRenderer;