import WebComponent from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/WebComponent";
import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";

import TitleLevel from "./types/TitleLevel";
import TitleRenderer from "./build/compiled/TitleRenderer.lit";

// Styles
import "./themes/sap_belize/Title.less";
import "./themes/sap_fiori_3/Title.less";

/**
 * @public
 */
const metadata = {
	tag: "ui5-title",
	styleUrl: [
		"Title.css"
	],
	usesNodeText: true,
	properties: /** @lends  sap.ui.webcomponents.main.Title.prototype */ {
		tooltip: {
			type : String,
			defaultValue : null
		},
		wrap: {
			type : Boolean
		},
		level: {
			type: TitleLevel,
			defaultValue : TitleLevel.H2
		}
		// textAlign : {type : "sap.ui.core.TextAlign", group : "Appearance", defaultValue : TextAlign.Initial},
	}
};

/**
 * @class
 * A constructor for <code>Title</code>
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.Title
 * @public
 */
class Title extends WebComponent {

	static get metadata() {
		return metadata;
	}

	static get renderer() {
		return TitleRenderer;
	}

	constructor(state) {
		super(state);
	}

	static calculateTemplateContext(state) {
		let context = {
			tag: (state.level === TitleLevel.Auto ? 'div' : state.level).toLowerCase(),
			ctr: state,
			classes: {
				main: {
					"sapMTitle": true,
					"sapMTitleWrap": state.wrap,
					"sapUiSelectable": true,
					["sapMTitleStyle" + state.level]: true
				}
			},
			styles: {
				main: {}
			}
		};

		return context;
	}

}

Core.boot().then(_ => {
	Title.define();
});

export default Title;

