import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const LabelLitRenderer = {};

const block0 = (context) => { return html`<label 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"	class="${lit.ifTruthy(context.classes.main)}"	style="${lit.ifTruthy(context.styles.main)}"	dir="${lit.ifTruthy(context.dir)}"	for="${lit.ifTruthy(context.labelFor)}"><bdi id="${lit.ifTruthy(context.ctr._id)}-bdi">			${lit.ifTruthy(context.ctr._nodeText)}</bdi></label>`; };
const renderMe = block0;

LabelLitRenderer.render = renderMe;

export default LabelLitRenderer;