import WebComponent from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/WebComponent";
import ButtonRenderer from "./build/compiled/ButtonRenderer.lit";
import ButtonType from "./types/ButtonType";
import TextDirection from "./types/TextDirection";
import ButtonTemplateContext from "./ButtonTemplateContext";
import URI from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/types/URI";
import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";
import KeyCodes from "@openui5/sap.ui.core/dist/sap/ui/events/KeyCodes";
import ShadowDOM from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/compatibility/ShadowDOM";

// Styles
import belize from "./themes/sap_belize/Button.less";
// eslint-disable-next-line
import fiori_3 from "./themes/sap_fiori_3/Button.less";

ShadowDOM.registerStyle("sap_belize", "Button.css", belize);

const metadata = {
	tag: "ui5-button",
	styleUrl: [
		"Button.css"
	],
	usesNodeText: true,
	properties: /** @lends  sap.ui.webcomponents.main.Button.prototype */ {
		/**
		 * Defines the <code>Button</code> type.
		 */
		type: {type: ButtonType, defaultValue: ButtonType.Default},

		/**
		 * Determines whether the <code>Button</code> is enabled (default is set to <code>true</code>).
		 * A disabled <code>Button</code> has different colors depending on the {@link sap.m.ButtonType ButtonType}.
		 */
		disabled: {type: Boolean},

		/**
		 * Defines the icon to be displayed as graphical element within the <code>Button</code>.
		 * It can be an image or an icon from the icon font.
		 */
		icon: {type: URI, defaultValue: null},

		/**
		 * Determines whether the icon is displayed after the text.
		 * @type {boolean}
		 * @defaultvalue false
		 * @public
		 */
		iconLast: {type: Boolean},

		/**
		 * The source property of an alternative icon for the active (depressed) state of the button.
		 * Both active and default icon properties should be defined and have the same type: image or icon font.
		 * If the <code>icon</code> property is not set or has a different type, the active icon is not displayed.
		 */
		activeIcon: {type: URI, defaultValue: null},

		/**
		 * This property specifies the element's text directionality with enumerated options. By default, the control inherits text direction from the DOM.
		 */
		textDirection: {type: TextDirection, defaultValue: TextDirection.Inherit},

		/**
		 * Used to switch the active state (pressed or not) of the button
		 */
		_active: {type: Boolean},

		_iconSettings: { type: Object }
	},
	slots: {},
	events: /** @lends  sap.ui.webcomponents.main.Button.prototype */ {
		/**
		 * press event
		 * @event
		 * @public
		 */
		press: {}
	}
};

/**
 * @class
 *
 * It can be used for button.
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.Button
 * @extends sap.ui.webcomponents.base.WebComponent
 * @customtag ui5-button
 * @public
 */
class Button extends WebComponent {

	static get metadata() {
		return metadata;
	}

	static get renderer() {
		return ButtonRenderer;
	}

	static get calculateTemplateContext() {
		return ButtonTemplateContext.calculate;
	}

	constructor() {
		super();
	}

	onBeforeRendering() {
		if (this.icon) {
			this._iconSettings = {
				src: this._active && this.activeIcon ? this.activeIcon : this.icon
			};
		} else {
			this._iconSettings = null;
		}
	}

	ontap(_event) {
		if (!this.disabled) {
			this.fireEvent("press", {});
		}
	}

	ondown(_event) {
		if (this.activeIcon) {
			this._active = true;
		}
	}

	onup(_event) {
		if (this.activeIcon) {
			this._active = false;
		}
	}

	onkeydown(event) {
		if (event.which === KeyCodes.SPACE || event.which === KeyCodes.ENTER) {
			this._active = true;
		}
	}

	onkeyup(event) {
		if (event.which === KeyCodes.SPACE || event.which === KeyCodes.ENTER) {
			this._active = false;
		}
	}

	onfocusout(_event) {
			this._active = false;
	}
}

Core.boot().then(_ => {
	Button.define();
});

export default Button;