class DayPickerTemplateContext {

	static calculate(state) {

		let context = {
			ctr: state,
			classes: {
				main: {
					"sapUiCalMonthView": true,
					"sapUiCalNoWeekNum": !state.showWeekNumbers
				}
			},
			styles: {
				main: {
					"width": "100%",
					"display": state._hidden ? 'none' : 'block'
				}
			}
		};

		return context;
	}
}

export default DayPickerTemplateContext;