
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const CustomListItemLitRenderer = {};

const block0 = (context) => { return html`<li 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"	tabindex="${lit.ifTruthy(context.ctr._tabIndex)}"	style="${lit.ifTruthy(context.styles.main)}"	class="${lit.ifTruthy(context.classes.main)}">		${ context.placeSelectionControlBefore ? block1(context) : undefined }<div id="${lit.ifTruthy(context.ctr._id)}-content" class="${lit.ifTruthy(context.classes.inner)}">			${ repeat(context.ctr.content, undefined, (item, index) => block5(item, index, context)) }</div>		${ context.placeSelectionControlAfter ? block6(context) : undefined }</li>`; };
const block1 = (context) => { return html`${ context.modeSingleSelect ? block2(context) : undefined }${ context.modeMultiSelect ? block3(context) : undefined }${ context.modeDelete ? block4(context) : undefined }`; };
const block2 = (context) => { return html`<ui5-radiobutton				id="${lit.ifTruthy(context.ctr._id)}-singleSelectionControl"				class="radioButtonInListItem"				selected="${lit.ifTruthy(context.ctr.selected)}"></ui5-radiobutton>	`; };
const block3 = (context) => { return html`<ui5-checkbox				id="${lit.ifTruthy(context.ctr._id)}-multiSelectionControl"				selected="${lit.ifTruthy(context.ctr.selected)}"></ui5-checkbox>	`; };
const block4 = (context) => { return html`<ui5-button				id="${lit.ifTruthy(context.ctr._id)}-deleteSelectionControl"				type="Transparent"				icon="sap-icon://delete"				@press="${lit.ifTruthy(context.ctr.fnOnDelete)}"		></ui5-button>	`; };
const block5 = (item, index, context) => { return html`<slot name="${lit.ifTruthy(item._slot)}" />    `; };
const block6 = (context) => { return html`${ context.modeSingleSelect ? block7(context) : undefined }${ context.modeMultiSelect ? block8(context) : undefined }${ context.modeDelete ? block9(context) : undefined }`; };
const block7 = (context) => { return html`<ui5-radiobutton				id="${lit.ifTruthy(context.ctr._id)}-singleSelectionControl"				class="radioButtonInListItem"				selected="${lit.ifTruthy(context.ctr.selected)}"></ui5-radiobutton>	`; };
const block8 = (context) => { return html`<ui5-checkbox				id="${lit.ifTruthy(context.ctr._id)}-multiSelectionControl"				selected="${lit.ifTruthy(context.ctr.selected)}"></ui5-checkbox>	`; };
const block9 = (context) => { return html`<ui5-button				id="${lit.ifTruthy(context.ctr._id)}-deleteSelectionControl"				type="Transparent"				icon="sap-icon://delete"				@press="${lit.ifTruthy(context.ctr.fnOnDelete)}"		></ui5-button>	`; };
const renderMe = block0;

CustomListItemLitRenderer.render = renderMe;

export default CustomListItemLitRenderer;