
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const ToggleButtonLitRenderer = {};

const block0 = (context) => { return html`<button 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"		class="${lit.ifTruthy(context.classes.main)}"		style="${lit.ifTruthy(context.styles.main)}"		disabled="${lit.ifTruthy(context.ctr.disabled)}"		aria-disabled="${lit.ifTruthy(context.ariaDisabled)}"		title="${lit.ifTruthy(context.ctr.tooltip)}"		tabindex="${lit.ifTruthy(context.tabindex)}"			aria-pressed="${lit.ifTruthy(context.pressed)}"	>		${ context.ctr._iconSettings ? block1(context) : undefined }${ context.ctr._nodeText ? block2(context) : undefined }</button>`; };
const block1 = (context) => { return html`<ui5-icon				class="${lit.ifTruthy(context.classes.icon)}"				src="${lit.ifTruthy(context.ctr._iconSettings.src)}"			></ui5-icon>		`; };
const block2 = (context) => { return html`<span id="${lit.ifTruthy(context.ctr._id)}-content" dir="${lit.ifTruthy(context.dir)}" class="${lit.ifTruthy(context.classes.text)}"><bdi>${lit.ifTruthy(context.ctr._nodeText)}</bdi></span>		`; };
const renderMe = block0;

ToggleButtonLitRenderer.render = renderMe;

export default ToggleButtonLitRenderer;