
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const InputLitRenderer = {};

const block0 = (context) => { return html`<div 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"	class="${lit.ifTruthy(context.classes.main)}"	style="${lit.ifTruthy(context.styles.main)}"	aria-invalid="${lit.ifTruthy(context.ariaInvalid)}"	aria-labelledBy="${lit.ifTruthy(context.ariaLabelledBy)}"><div id="${lit.ifTruthy(context.ctr._id)}-wrapper"	class="${lit.ifTruthy(context.classes.wrapper)}"><input id="${lit.ifTruthy(context.ctr._id)}-inner"			class="sapMInputBaseInner"			type="${lit.ifTruthy(context.type)}"			?disabled="${lit.ifTruthy(context.ctr.disabled)}"			?readonly="${lit.ifTruthy(context._readOnly)}"			.value="${lit.ifTruthy(context.ctr.value)}"			name="${lit.ifTruthy(context.ctr.name)}"			placeholder="${lit.ifTruthy(context.ctr.placeholder)}"			data-sap-no-tab-ref			data-sap-focus-ref	/>		${ context.ctr.icon ? block1(context) : undefined }</div>	${ context.ctr.showSuggestion ? block2(context) : undefined }</div>`; };
const block1 = (context) => { return html`<slot name="${lit.ifTruthy(context.ctr.icon._slot)}"></slot>		`; };
const block2 = (context) => { return html`<ui5-popover				placement-type="Bottom"				hide-header="true"				hide-arrow="true"				horizontal-align="Stretch"				initial-focus="${lit.ifTruthy(context.ctr._id)}-inner"			><ui5-list>						${ repeat(context.ctr.suggestionItems, undefined, (item, index) => block3(item, index, context)) }</ui5-list></ui5-popover>	`; };
const block3 = (item, index, context) => { return html`<slot name="${lit.ifTruthy(item._slot)}"></slot>						`; };
const renderMe = block0;

InputLitRenderer.render = renderMe;

export default InputLitRenderer;