import WebComponent from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/WebComponent";
import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";
import TextAlign from "./types/TextAlign";
import TemplateHelper from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/TemplateHelper";
import TableCellRenderer from "./build/compiled/TableCellRenderer.lit";

// Styles
import "./themes/sap_belize/TableCell.less";
import "./themes/sap_fiori_3/TableCell.less";

/**
 * @public
 */
const metadata = {
	tag: "ui5-table-cell",
	styleUrl: [
		"TableCell.css"
	],
	slots: {
		/**
		 * This property specifies the content of the <code>Cell</code>
		 * @type {HTMLElement}
		 * @public
		 */
		content: {
			type: HTMLElement,
			multiple: true
		}
	},
	properties: /** @lends sap.ui.webcomponents.main.TableCell.prototype */ {

		_firstInRow: {
			type: Boolean
		},
		_lastInRow: {
			type: Boolean
		},
		_hAlign: {
			type: TextAlign,
			defaultValue: TextAlign.Begin
		},
		_hasBorder: {
			type: Boolean,
			defaultValue: false
		}
	},
	events: {
	}
};

/**
 * @class
 *
 * Constructor for a new <code>Cell</code>.
 *
 * The <code>Cell</code> element defines the structure of the data in one <code>Cell</code> from the <code>Row</code>
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.TableCell
 * @public
 */
class TableCell extends WebComponent {

	static get metadata() {
		return metadata;
	}

	static get renderer() {
		return TableCellRenderer;
	}

	static calculateTemplateContext(state) {
		let context = {
			ctr: state,
			classes: {
				main: {
					"sapWCTableCell": true,
					"sapWCTableCellFirst": state._firstInRow,
					"sapWCTableCellLast": state._lastInRow,
					"sapWCTableCellWithBorder": state._hasBorder
				}
			},
			styles: {
				main: {
				}
			}
		};

		// to be changed once we support RTL
		const textAlign = TemplateHelper.getTextAlign(state._hAlign, "LTR");

		if (textAlign) {
			context.styles.main["text-align"] = textAlign;
		}

		return context;
	}
}

Core.boot().then(_ => {
	TableCell.define();
});

export default TableCell;