import Core from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/Core";
import TabSeparatorTemplateContext from "./TabSeparatorTemplateContext";
import TabBase from "./TabBase";

import TabSeparatorRenderer from "./build/compiled/TabSeparatorRenderer.lit";

const metadata = {
	tag: "ui5-tab-separator",
	styleUrl: [],
	properties: {
	},
	events : {

	}
};

/**
 * @class
 * Represents an Icon used to separate 2 tabs inside a TabContainer.
 *
 * @constructor
 * @author SAP SE
 * @alias sap.ui.webcomponents.main.TabSeparator
 * @public
 */
class TabSeparator extends TabBase {

	static get metadata() {
		return metadata;
	}

	static get renderer() {
		return TabSeparatorRenderer;
	}

	constructor(state) {
		super(state);
	}

	static get calculateTemplateContext() {
		return TabSeparatorTemplateContext.calculate;
	}
}

Core.boot().then(_ => {
	TabSeparator.define();
});

export default TabSeparator;
