
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const PopoverLitRenderer = {};

const block0 = (context) => { return html`<span 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}" class="${lit.ifTruthy(context.classes.frame)}"><span id="${lit.ifTruthy(context.ctr._id)}-firstfe" tabindex="0"></span><div style="${lit.ifTruthy(context.styles.main)}" role="dialog" aria-labelledby="${lit.ifTruthy(context.headerId)}" tabindex="-1" class="${lit.ifTruthy(context.classes.main)}">			${ !context.ctr.hideHeader ? block1(context) : undefined }<div id="${lit.ifTruthy(context.ctr._id)}-content" role="application" style="${lit.ifTruthy(context.styles.content)}" class="sapMPopupContent"><div class="sapMPopupScroll">					${ repeat(context.ctr.content, undefined, (item, index) => block4(item, index, context)) }</div></div>			${ context.ctr.footer ? block5(context) : undefined }<span id="${lit.ifTruthy(context.ctr._id)}-arrow" style="${lit.ifTruthy(context.styles.arrow)}" class="${lit.ifTruthy(context.classes.arrow)}"></span></div><span id="${lit.ifTruthy(context.ctr._id)}-lastfe" tabindex="0"></span><div tabindex="0" id="${lit.ifTruthy(context.ctr._id)}-blocklayer" style="${lit.ifTruthy(context.styles.blockLayer)}" class="${lit.ifTruthy(context.classes.blockLayer)}"></div></span>`; };
const block1 = (context) => { return html`<header>			${ context.ctr.header ? block2(context) : block3(context) }</header>	`; };
const block2 = (context) => { return html`<div role="toolbar" class="sapMPopupHeader"><slot name="${lit.ifTruthy(context.ctr.header._slot)}"></slot></div>			`; };
const block3 = (context) => { return html`<h2 role="toolbar" class="sapMPopupHeader sapMPopupHeaderText">${lit.ifTruthy(context.ctr.headerText)}</h2>			`; };
const block4 = (item, index, context) => { return html`<slot name="${lit.ifTruthy(item._slot)}"></slot>					`; };
const block5 = (context) => { return html`<footer><div class="sapMPopupFooter"><slot name="${lit.ifTruthy(context.ctr.footer._slot)}"></slot></div></footer>	`; };
const renderMe = block0;

PopoverLitRenderer.render = renderMe;

export default PopoverLitRenderer;