
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const DayPickerLitRenderer = {};

const block0 = (context) => { return html`<div 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"        class="${lit.ifTruthy(context.classes.main)}"		style="${lit.ifTruthy(context.styles.main)}"><div role="row" style="overflow:hidden">		${ repeat(context.ctr._dayNames, undefined, (item, index) => block1(item, index, context)) }</div><div id="${lit.ifTruthy(context.ctr._id)}-days" class="sapUiCalItems" tabindex="-1">		${ repeat(context.ctr._days, undefined, (item, index) => block2(item, index, context)) }</div></div>`; };
const block1 = (item, index, context) => { return html`<div				id=${lit.ifTruthy(item._id)}				role="columnheader"				aria-label="${lit.ifTruthy(item.name)}"				class="${lit.ifTruthy(item.classes)}">				${lit.ifTruthy(item.ultraShortName)}</div>		`; };
const block2 = (item, index, context) => { return html`<div				id="${lit.ifTruthy(item.id)}"				tabindex="${lit.ifTruthy(item._tabIndex)}"				data-sap-timestamp="${lit.ifTruthy(item.timestamp)}"				role="gridcell"				aria-selected="${lit.ifTruthy(item.selected)}"				class="${lit.ifTruthy(item.classes)}"><span class="sapUiCalItemText">						${lit.ifTruthy(item.iDay)}</span>					${ item.iWeekNumber ? block3(item, index, context) : undefined }</div>		`; };
const block3 = (item, index, context) => { return html`<span class="sapUiCalWeekNum">${lit.ifTruthy(item.iWeekNumber)}</span>					`; };
const renderMe = block0;

DayPickerLitRenderer.render = renderMe;

export default DayPickerLitRenderer;