import DataType from "@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/types/DataType";

const LabelDesigns = {
	/**
	 * Displays the label in bold.
	 */
	Bold: "Bold",

	/**
	 * Displays the label in normal mode.
	 */
	Standard: "Standard"
};

class LabelDesign extends DataType {
	static isValid(value) {
		return !!LabelDesigns[value];
	}
}

LabelDesign.generataTypeAcessors(LabelDesigns);

export default LabelDesign;
