
/* eslint no-unused-vars: 0 */	
import lit from '@openui5/sap.ui.webcomponents.base/src/sap/ui/webcomponents/base/thirdparty/lit-html-bundle';

const html = lit.html;
const svg = lit.svg;
const repeat = lit.repeat;

const CheckBoxLitRenderer = {};

const block0 = (context) => { return html`<div 	id="${lit.ifTruthy(context.ctr._id)}"	data-sap-ui="${lit.ifTruthy(context.ctr._id)}"	aria-hidden="${lit.ifTruthy(context.ariaHidden)}"	class="${lit.ifTruthy(context.classes.main)}"	style="${lit.ifTruthy(context.styles.main)}"	role="checkbox"	aria-checked="${lit.ifTruthy(context.ctr.selected)}"	aria-readonly="${lit.ifTruthy(context.ariaReadonly)}"	tabindex="${lit.ifTruthy(context.tabIndex)}"	title="${lit.ifTruthy(context.tooltip)}"><div id="${lit.ifTruthy(context.ctr._id)}-CbBg" class="${lit.ifTruthy(context.classes.inner)}"><input id="${lit.ifTruthy(context.ctr._id)}-CB" type='checkbox' ?checked="${lit.ifTruthy(context.ctr.selected)}" name="${lit.ifTruthy(context.ctr.name)}" ?readonly="${lit.ifTruthy(context.ctr.readOnly)}" data-sap-no-tab-ref/></div>		${ context.ctr._label ? block1(context) : undefined }</div>`; };
const block1 = (context) => { return html`<ui5-label				class="labelInCheckbox"				wrap="${lit.ifTruthy(context.ctr._label.wrap)}"				text-direction="${lit.ifTruthy(context.ctr._label.textDirection)}"		>${lit.ifTruthy(context.ctr._label.text)}</ui5-label>		`; };
const renderMe = block0;

CheckBoxLitRenderer.render = renderMe;

export default CheckBoxLitRenderer;